.App {
  font-family: sans-serif;
  text-align: center;
  margin: auto;
}
.submitRequest {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
body {
  font-family: "Josefin Sans", sans-serif;
  font-family: "Lobster", cursive;
  background-color: #cbd2d0;
  color: black;
}
h4 {
  margin: 0 0px;
}
.dropDown {
  margin-top: 10px;
}
.flex {
  width: 1000px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.main {
  margin: auto;
}
.ToggleButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.image {
  margin-right: 50px;
}
